import React from 'react';
import {render, fireEvent} from '@testing-library/react-native';
import Button from '../Button';

describe('Button', () => {
  test('renders correctly', () => {
    const {getByTestId} = render(<Button />);
    const button = getByTestId('button');
    expect(button).toBeTruthy();
  });

  test('executes onPress callback when pressed', () => {
    const onPressMock = jest.fn();
    const {getByTestId} = render(<Button onPress={onPressMock} />);
    const button = getByTestId('button');
    fireEvent.press(button);
    expect(onPressMock).toHaveBeenCalledTimes(1);
  });

  test('applies disabled style when disabled prop is true', () => {
    const {getByTestId} = render(<Button disabled />);
    const button = getByTestId('button');
    expect(button.props.style[1]).toEqual({backgroundColor: '#3f3f46'});
  });
});
